import {request} from "../../../ts/util/utils";
import {Languages} from "../../../ts/util/Languages";


const lpg = Languages.package;
let clng = 'en';

if (localStorage.getItem("lngPkg") != null || localStorage.getItem("lngPkg") != undefined) {
    const currentlng = localStorage.getItem("lngPkg");
    clng = currentlng.replace(/["""]/g, "");


}



let html = template('tpl' , {request , lng :Languages.package});
document.getElementById('sec').innerHTML = html;

document.getElementById('btnSearch').addEventListener('click', function (evt) {
	let val = (document.getElementById('iptKeyword') as HTMLInputElement).value;
	if (val) {
		//location.href = './buyer3.html?by=search'+ (request['from']?'&from='+request['from']: '') +  (request['info']?'&info='+request['info']: '') +'&keyword=' + val;
		location.href = './index.html?by=search'+ (request['from']?'&from='+request['from']: '') +  (request['info']?'&info='+request['info']: '') +'&keyword=' + val;
	}
}, false);



$("#iptKeyword").attr('placeholder',lpg.searchtips);